"use client";
import { useRouter } from "next/navigation";

export default function Form({ title, fields, values, onChange, onSubmit, redirect }) {
  const router = useRouter();

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-200">
      {/* Formul√°rio centralizado */}
      <div className="bg-white p-8 rounded-2xl shadow-xl w-full max-w-lg">
        <h2 className="text-3xl font-bold text-gray-800 mb-6 text-center">{title}</h2>

        <form onSubmit={onSubmit} className="space-y-4">
          {fields.map((field) => (
            <div key={field.name}>
              <label className="block text-gray-700 mb-1">{field.label}</label>
              <input
                type={field.type || "text"}
                name={field.name}
                value={values[field.name] !== undefined ? values[field.name] : ""}
                onChange={onChange}
                required={field.required}
                className="w-full p-3 border border-gray-300 bg-gray-100 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 transition-all"
              />
            </div>
          ))}

          {/* Bot√µes de A√ß√£o */}
          <div className="flex gap-4">
            <button
              type="submit"
              className="w-full bg-blue-600 text-white font-semibold p-3 rounded-lg hover:bg-blue-700 transition-all"
            >
              Salvar
            </button>

            <button
              type="button"
              onClick={() => router.push('/clientes')}
              className="w-full bg-red-500 text-gray-900 font-semibold p-3 rounded-lg hover:bg-red-600 transition-all"
            >
              Cancelar
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
