"use client";
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import api from "@/lib/api";
import Form from "@/components/form";

export default function ProdutoForm({ produtoId }) {
  const router = useRouter();
  const isEditing = Boolean(produtoId);

  const [produto, setProduto] = useState({
    codigo: "",
    descricao: "",
    codigoBarras: "",
    valorVenda: "",
    pesoBruto: "",
    pesoLiquido: "",
  });

  useEffect(() => {
    if (isEditing) {
      api.get(`/produtos/${produtoId}`)
        .then((response) => setProduto(response.data))
        .catch((error) => console.error("Erro ao buscar produto:", error));
    }
  }, [produtoId, isEditing]);

  const handleChange = (e) => {
    let { name, value, type } = e.target;

    if (type === "number") {
      value = value.replace(/[^0-9.]/g, "").replace(/^([0-9]*\.?[0-9{0,2}]).*$/, "$1");
    }

    setProduto({ ...produto, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const token = localStorage.getItem("token");
      if (!token) {
        alert("Usu√°rio n√£o autenticado");
        return;
      }

      const payload = { ...produto, usuarioNome: "-" };
      const config = { headers: { Authorization: `Bearer ${token}` } };

      if (isEditing) {
        await api.put(`/produtos/${produtoId}`, payload, config);
      } else {
        await api.post("/produtos", payload, config);
      }

      router.push("/produtos");
    } catch (error) {
      console.error("Erro ao salvar produto:", error.response?.data || error);
      alert("Erro ao salvar produto. Veja o console.");
    }
  };

  const fields = [
    { name: "codigo", label: "C√≥digo", required: true, type: "number", disabled: isEditing },
    { name: "descricao", label: "Descri√ß√£o", required: true },
    { name: "codigoBarras", label: "C√≥digo de Barras", required: true },
    { name: "valorVenda", label: "Valor de Venda", required: true, type: "number", step: "0.01" },
    { name: "pesoBruto", label: "Peso Bruto (kg)", type: "number", step: "0.01" },
    { name: "pesoLiquido", label: "Peso L√≠quido (kg)", type: "number", step: "0.01" },
  ];

  return (
    <Form
      title={isEditing ? "‚úèÔ∏è Editar Produto" : "üÜï Criar Produto"}
      fields={fields}
      values={produto}
      onChange={handleChange}
      onSubmit={handleSubmit}
    />
  );
}
